import React, { useEffect } from 'react';
import { motion } from "framer-motion";
import Link from '@material-ui/core/Link';
import { CSSTransition } from "react-transition-group";
import Fab from '@material-ui/core/Fab';
import Tooltip from '@material-ui/core/Tooltip';
import Rating from '@material-ui/lab/Rating';
import { Button } from 'react-bootstrap';
import { Scrollbars } from 'react-custom-scrollbars';

import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import CancelIcon from '@material-ui/icons/Cancel';
import EditIcon from '@material-ui/icons/Edit';
import LanguageIcon from '@material-ui/icons/Language';
import SignalCellular1BarIcon from '@material-ui/icons/SignalCellular1Bar';
import SignalCellular2BarIcon from '@material-ui/icons/SignalCellular2Bar';
import SignalCellular4BarIcon from '@material-ui/icons/SignalCellular4Bar';

// Temporary
import bg1 from './Default Backgrounds/bg1.jpg';
import bg2 from './Default Backgrounds/bg2.jpg';
import bg3 from './Default Backgrounds/bg3.gif';

const tileDescriptionStyle = {
    position: 'fixed',
    top: 350,
    background: 'white',
    width: '100%',
    left: 0,
    height: 'calc(100% - 350px)',
    overflowX: 'hidden',
    zIndex: 99999999999999
}

const tileDescriptionContainerStyle = {
    width: '100%',
    minHeight: '100%',
    background: 'var(--container-gradient)',
    padding: '2rem'
}

const container = {
    hidden: { opacity: 0 },
    show: {  opacity: 1 }
}

// TODO: Display tutor's profile picture
function Tile({title, level, description, prerequisites, numberOfLessons, cover, facilitatorName, facilitatorUsername, facilitatorPicture, facilitatorCourses, editable, rating, enrollees, syllabus}) {
    const [pictureHover, setPictureHover] = React.useState(false);
    const [tileHover, setTileHover] = React.useState(false);
    const [changePadding, setChangePadding] = React.useState(false);
    const [open, setOpen] = React.useState(false);

    useEffect(() => {
        if (tileHover) {
            setChangePadding(true);
        } else {
            setTimeout(() => {
                setChangePadding(false);
            }, 300);
        }
    }, [tileHover]);

    useEffect(() => {
        if (!open) setTileHover(false);
    }, [open]);

    const openHandler = () => {
        setOpen(true);
    };

    const closeHandler = () => {
        setOpen(false);
    };

    return (
        <>
            <motion.div layout className={`tile ${open ? 'open' : ''}`}
                    style={{backgroundImage: `url(${bg2})`, backgroundSize: 'cover', padding: changePadding ? '30px 30px 5px 30px' : '30px'}}
                    whileHover={!open && { boxShadow: ` 0px 0px 3.6px rgba(0, 0, 0, 0.024),
                                                    0px 0px 10px rgba(0, 0, 0, 0.035),
                                                    0px 0px 24.1px rgba(0, 0, 0, 0.046),
                                                    0px 0px 80px rgba(0, 0, 0, 0.07)`, transition: { duration: 0.3 } }}
                    onMouseEnter={() => !open && setTileHover(true)} 
                    onMouseLeave={() => !open && setTileHover(false)} 
                    transition={{ duration: 0.2 }}>
                {open && <motion.div className='overlay' variants={container} initial='hidden' animate='show'></motion.div>}
                <motion.div className='tile-content'>
                    <motion.div style={{display: 'flex', flexDirection: 'row', flex: 1}}>
                        <motion.div className={`tile-header ${open ? 'open' : ''}`}>
                            <motion.h2 layout>{title}</motion.h2>
                            <motion.h4 layout>{numberOfLessons} Lessons {open ? '| 2h classes' : ''}</motion.h4>
                            {open && <span style={{color: 'white'}}><b>{enrollees}</b> already enrolled</span>}
                            {open && <motion.div style={{display: 'flex', flexDirection: 'row', alignItems: 'center', color: 'white', padding: '10px 0'}}>
                                <b style={{paddingRight: 5}}>{rating}</b>
                                <Rating name="read-only" value={rating} precision={0.5} readOnly />
                            </motion.div>}
                            {open && !editable && <Button style={{position: 'absolute', bottom: 30,  width: '10rem', height: '4rem', marginTop: 30, fontSize: '1.1em', padding: 10}} >Enroll<br/>Starts Oct 15</Button>}
                        </motion.div>
                        {open && <Link style={{transition: 'box-shadow 0.3s ease'}} onClick={closeHandler}><CancelIcon className='close-btn' style={{color: 'white'}}/></Link>}
                    </motion.div>
                    <motion.div className="tile-footer" onMouseLeave={() => setPictureHover(false)} style={{marginBottom: open ? 20 : 0, justifyContent: 'flex-end'}}>
                        {editable && !open && <Tooltip title="Edit course" placement='top'>
                            <Fab color="secondary" aria-label="edit" color='primary'>
                                <EditIcon />
                            </Fab>
                        </Tooltip>}
                        {!editable && <div style={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                            <CSSTransition in={open || pictureHover} unmountOnExit timeout={300} classNames='facilitator-info-slide'>
                                <motion.div className="facilitator-info" onMouseLeave={() => setPictureHover(false) }>
                                    <Link className='facilitator-name' href={`/${facilitatorUsername}`} style={{color: open ? 'white' : 'black'}} >{facilitatorName}</Link>
                                    <motion.span style={{color: open ? 'white' : 'black'}} >has {facilitatorCourses} more courses</motion.span>
                                </motion.div>
                            </CSSTransition>
                            <motion.div className='profile-image-container' style={{width: '4rem', height: '4rem'}}>
                                <motion.img src={facilitatorPicture} alt="profile" className='profile-image' onMouseEnter={() => setPictureHover(true)}/>
                            </motion.div>
                        </div>}
                        {editable && open && <span style={{color: 'white'}}>You are previewing this course</span>}
                    </motion.div>
                    <CSSTransition in={!open && tileHover} unmountOnExit timeout={300} classNames='up-arrow'>
                        <Link style={{alignSelf: 'center'}} onClick={openHandler} ><KeyboardArrowUpIcon fontSize='large'/></Link>
                    </CSSTransition>
                </motion.div>
            </motion.div>
            {open && <motion.div style={tileDescriptionStyle} layout>
                <Scrollbars renderTrackHorizontal={props => <div {...props} className="track-horizontal"/>}
                            renderTrackVertical={props => <div {...props} className="track-vertical"/>}
                            renderThumbHorizontal={props => <div {...props} className="thumb-horizontal"/>}
                            renderThumbVertical={props => <div {...props} className="thumb-vertical"/>}
                            renderView={props => <div {...props} className="view"/>}
                            autoHide autoHideTimeout={1000} 
                            autoHideDuration={200}>
                    <motion.div style={tileDescriptionContainerStyle}>
                        <motion.div style={{display: 'flex', flexDirection: 'column', float: 'right'}}>
                            <motion.div style={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                                <LanguageIcon />
                                <span style={{fontSize: 16, marginLeft: 10}}>English</span>
                            </motion.div>
                            <motion.div style={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                                {level === 'easy' && <SignalCellular1BarIcon />}
                                {level === 'moderate' && <SignalCellular2BarIcon />}
                                {level === 'hard' && <SignalCellular4BarIcon />}
                                {level === 'easy' && <span style={{fontSize: 16, marginLeft: 10, paddingTop: 10}}>Beginner Level</span>}
                                {level === 'moderate' && <span style={{fontSize: 16, marginLeft: 10, paddingTop: 10}}>Intermediate Level</span>}
                                {level === 'hard' && <span style={{fontSize: 16, marginLeft: 10, paddingTop: 10}}>Advanced Level</span>}
                            </motion.div>
                        </motion.div>
                        <motion.div style={{paddingTop: 80}}>
                            <h1>About this Course</h1>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Tenetur, asperiores magnam vel autem soluta non et vero. Quae dignissimos repudiandae minima asperiores a, debitis similique expedita saepe distinctio! Iusto, autem. Qui alias temporibus modi dicta maiores earum cum optio, odio, minus excepturi ullam aperiam atque accusamus deleniti eum totam necessitatibus.</p>
                            <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Odio, labore reiciendis. Eius ad minima provident cum, totam autem aliquid eaque, nobis vitae eos quaerat, sint necessitatibus quasi molestias labore velit accusamus. Ipsam quidem excepturi repellendus explicabo? Cumque ipsam cum accusantium, porro dolorum ad quaerat amet at minima mollitia ullam labore. Adipisci, ex architecto quibusdam sequi distinctio quos consequuntur numquam quam eaque nostrum magni omnis ducimus praesentium quod qui id ab aperiam animi voluptatum voluptas consectetur suscipit nemo. Neque, molestiae? Rem, iste soluta. Suscipit itaque ratione, eaque cupiditate in accusantium doloremque nisi aliquid, quae possimus non adipisci doloribus odit, aliquam ipsum.</p>
                        </motion.div>
                        <motion.div style={{marginTop: 30}}>
                            <h1>Prerequisites</h1>
                            <span>Lorem ipsum dolor sit amet consectetur adipisicing elit. Fuga ad adipisci saepe laboriosam deleniti tempora sed magni dignissimos inventore suscipit veritatis vel sit facilis velit beatae molestias minima quam facere corrupti assumenda, harum nihil illo accusantium esse! Quod quas mollitia, a consequatur, vitae, cupiditate aperiam quidem sapiente quos praesentium molestiae?</span>
                        </motion.div>
                        <motion.div style={{marginTop: 30}}>
                            <h1>What you'll learn from this Course</h1>
                            {syllabus.map((item, index) => {
                                return (
                                    <motion.div style={{padding: '10px 0', display: 'grid', gridTemplateColumns: '1fr 6fr', gridGap: '10px', alignItems: 'center'}} key={index}>
                                        <h2 style={{paddingRight: 30, margin: 0}}>Week {index + 1}</h2>
                                        <p style={{margin: 0}}>{item}</p>
                                    </motion.div>
                                )
                            })}
                        </motion.div>
                    </motion.div>
                </Scrollbars>
            </motion.div>}
        </>
    )
}

function TileList(props) {
    return (
        <motion.div className="tile-list">
            {props.children}
        </motion.div>
    )
};

export default TileList;

export {
    Tile
}