import React, {useState} from 'react';
import {Container, TextField, makeStyles, Button, IconButton} from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/AddCircle';

const useStyles = makeStyles((theme) => ({
    root : {
        border: "1px ",
        padding: "10px",
        borderRadius: "6px"
    }
}))

const formGroup3 = {
    width: '100%',
    display: 'grid',
    alignItems: 'center',
    gridTemplateColumns: '6fr 1fr 1fr',
    gridColumnGap: '10px',
    paddingTop: 10
}

const formContentStyle = {
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-around',
    margin: 10,
    paddingTop: 10,
}

const StepTwo = ({inputs, setInputs, setValidate}) => {
    const handleChange = (index, event) =>{
        const {name, value} = event.target;
        const values = [...inputs];
        values[index][name] = value;
        setInputs(values);
    }

    const handleSubmit = (event) =>{
        event.preventDefault();
        console.log(inputs);
    }

    const handleAdd = () => {
        setInputs([...inputs, {lesson : ""}])
    }

    const handleDelete = (index) => {
        const values = [...inputs]
        if (values.length != 1) values.splice(index, 1);
        setInputs(values);
    }

    function getError(){
        for (let i = 0; i < inputs.length; i++){
            if(inputs[i].lesson == "") return "Enter content for lesson " + (i+1);
        }
        // 
        return "Proceed..."
    }

    const errorColor = getError() != "Proceed..." ? "red" : "green";
    setValidate(getError() != "Proceed..." ? false: true);

    const classes = useStyles();

    return (
        <>
            <form className={classes.root} style={formContentStyle}>
            <h5 style={{color:errorColor}}>{getError()}</h5>
            <h2>Course Content</h2>
                {inputs.map((input, index) => (
                    <div key={index} style={formGroup3}>
                        <TextField
                        name="lesson"
                        label = {"Lesson "+(index+1)}
                        variant="outlined"
                        value={input.lesson}
                        autoComplete="off"
                        onChange={event => handleChange(index, event)}/>
                        
                        <IconButton label="delete" onClick={() => handleDelete(index)}>
                            <DeleteIcon />
                        </IconButton>

                        <IconButton label="add" onClick={() => handleAdd()}>
                            <AddIcon />
                        </IconButton>
                    </div>
                ))}
            </form>
        </>
    )
}

export default StepTwo;
