import React, {useState} from 'react';
import Container from '../../../components/Container/Container';
import { Button } from 'react-bootstrap';
import Typography from '@material-ui/core/Typography';
import StepOne from './StepOne'
import StepTwo from './StepTwo';
import StepThree from './StepThree'
import StepFour from './StepFour'
import { SwitchTransition, CSSTransition } from "react-transition-group";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useHistory } from "react-router-dom";

import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import Alert from '@material-ui/lab/Alert';

import FolderIcon from "./Components/assets/folder_icon_transparent.png";

const steps = ['Plan your course overview', 'Create course content', 'Describe your course', 'Publish Your Course'];

const {REACT_APP_GOOGLE_CLIENT_ID, REACT_APP_API_URL} = process.env;

const initialvalues = {
    courseName : "",
    difficulty : "",
    category : "",
    noOfStudents : "",
    searchTags : []
}

const stepFooterStyle = {
	display : 'flex',
	justifyContent : 'space-between',
	position : 'fixed',
	width : '100%',
	right: 0,
	bottom: 0,
	padding : '10px 20px',
	backdropFilter: 'blur(25px)',
    zIndex: 99999,
}

const stepContainerStyle = {
	paddingBottom: 30
}

const level = ["Easy", "Moderate", "Hard"]

export default function CreateCourse({userData}) {
	//console.log(userData.id);
	const [activeStep, setActiveStep] = useState(0);
	const [forward, setForward] = React.useState(true);
	const [skipped, setSkipped] = useState(new Set());
	const [values, setValues] = useState(initialvalues);
	const [personName, setPersonName] = useState([]);		// search tags
	const validates = [false, false, false]
	const [validate, setValidate] = useState(false);
	const props1 = {values, setValues, personName, setPersonName, setValidate};
	validates[activeStep] = validate
	const [submitting, setSubmitting] = React.useState(false);
	const [error, setError] = React.useState('');

	// step 2 props
	const [inputs, setInputs] = useState([
		{lesson : ""},
		{lesson : ""},
		{lesson : ""}
	])

	const props2 = {inputs, setInputs, setValidate};

	// step 3 props
	const [description, setDescription] = useState("");

	const [reqs, setReqs] = useState([
		{lesson : ""}
	]);

	const props3 = {description, setDescription, reqs, setReqs, setValidate};

	// step 4 props
	const [amount, setAmount] = useState("")
	const [publish, setPublish] = useState(false)
	const [img, setImg] = useState("")
	const [image, setImage] = useState(FolderIcon);
    const [isUploaded, setIsUploaded] = useState(false);
    const [typeFile, setTypeFile] = useState("");

	const props4 = {amount, setAmount, publish, setPublish, image, setImage, isUploaded, setIsUploaded, typeFile, setTypeFile};

	const isStepOptional = (step) => {
		return step === 1;
	};

	const isStepSkipped = (step) => {
		return skipped.has(step);
	};

	const [open, setOpen] = useState(false);

	// const handleClickOpen = () => {
	// 	setOpen(true);
	// };
	const history = useHistory();

	const handleClose = () => {
		history.push("/courses");
		//setOpen(false);
	};

	const trySubmit = () => {
        setSubmitting(true);
		var lessons = [];
		inputs.forEach(element => {
			lessons.push(element["lesson"]);
		})

		var requirements = [];
		reqs.forEach(element => {
			requirements.push(element["lesson"]);
		})

		console.log(lessons);
		
        const requestOptions = {
            method: 'POST',
            mode: 'cors', // Enable cross-origin resource sharing
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
				'Authorization' : `Bearer ${userData.accessToken.token} ${userData.id}`
            },
            body: JSON.stringify({ 
                title: values.courseName,
				description: description,
				level: level[values.difficulty-1],
				author: "615565bbc7637a05131a72ba",
				numberOfLessons: inputs.length,
				isPublished: publish,
				noOfStudents: values.noOfStudents,
				category: values.category,
				searchTags: personName,
				lessons: lessons,
				requirements: requirements,
				amount: amount
			})
        };
        fetch(REACT_APP_API_URL + '/course/create', requestOptions)
        .then(async response => {
            const data = await response.json();
            if (response.status >= 500) return Promise.reject("Server error! Please try again");

            if (!data.success) {
                const err = data.message;
				console.log(err);
                return Promise.reject(err);
            }

            //handleContinue();
        })
        .catch(err => {
            setSubmitting(false);
            setError(err);
        });
		setOpen(true);
        setSubmitting(false);
    }

	const handleNext = () => {
		// let newSkipped = skipped;
		// if (isStepSkipped(activeStep)) {
		// 	newSkipped = new Set(newSkipped.values());
		// 	newSkipped.delete(activeStep);
		// }

		// setActiveStep((prevActiveStep) => prevActiveStep + 1);
		// setSkipped(newSkipped);
		if (activeStep < steps.length - 1) {
			setForward(true);
			setActiveStep(activeStep + 1);
		}
		if (activeStep == steps.length - 1){
			trySubmit();
		}
	};

	const handleBack = () => {
		setForward(false);
		setTimeout(() => {
			setActiveStep((prevActiveStep) => prevActiveStep - 1);
		}, 10);
		setTimeout(() => {
			setForward(true);
		}, 1000);
	};

	const handleSkip = () => {
		if (!isStepOptional(activeStep)) {
			// You probably want to guard against something like this,
			// it should never occur unless someone's actively trying to break something.
			throw new Error("You can't skip a step that isn't optional.");
		}

		setActiveStep((prevActiveStep) => prevActiveStep + 1);
		setSkipped((prevSkipped) => {
			const newSkipped = new Set(prevSkipped.values());
			newSkipped.add(activeStep);
			return newSkipped;
		});
	};

	const handleReset = () => {
		setActiveStep(0);
		setValues(initialvalues);
		setPersonName([])
	};

	function renderStep(idx){
		switch(idx) {
			case 0:
				return <StepOne {...props1} />;
			case 1:
				return <StepTwo {...props2}/>;
			case 2:
				return <StepThree {...props3}/>;
			case 3:
				return <StepFour {...props4}/>;
		}
	}

	const handleStep = (step) => {

	};

	return (
		<Container noSearch hasStepper heading='Create a new Course' steps={steps} handleStep={handleStep} activeStep={activeStep}>
			<Dialog
				open={open}
				onClose={handleClose}
				aria-labelledby="alert-dialog-title"
				aria-describedby="alert-dialog-description"
			>
				<DialogTitle id="alert-dialog-title">
				{"Succesfully Created the Course !"}
				</DialogTitle>
				<DialogContent>
				<DialogContentText id="alert-dialog-description">
					Your course has been created successfully. Find it in the course catalog
				</DialogContentText>
				</DialogContent>
				<DialogActions>
				<Button onClick={handleClose} autoFocus>Done</Button>
				</DialogActions>
			</Dialog>
			{activeStep === steps.length ? (
			<React.Fragment>
				<Typography sx={{ mt: 2, mb: 1 }}>
				All steps completed - you&apos;re finished
				</Typography>
				<Button onClick={handleReset}>Reset</Button>
			</React.Fragment>
			) : (
			<div>
				<SwitchTransition mode='out-in'>
					<CSSTransition
						key={activeStep}
						addEndListener={(node, done) => {
							node.addEventListener("transitionend", done, false);
						}}
						classNames="slide" >
						<div style={stepContainerStyle} className={!forward ? 'back' : ''}>
							{renderStep(activeStep)}
						</div>
					</CSSTransition>
				</SwitchTransition>
				<div style={stepFooterStyle}>
					<Button variant="outline-secondary" disabled={activeStep === 0} onClick={handleBack} style={{width: 90, height: 40, fontSize: 16}}>
						<div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
							<KeyboardArrowLeftIcon />
							<span>Back </span>
						</div>
					</Button>
					<Button onClick={handleNext} disabled={!validate} variant="outline-secondary" style={{width: 90, height: 40, fontSize: 16}}>
						<div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
							<span>{activeStep === steps.length - 1 ? 'Finish' : 'Next'}</span>
							{activeStep < steps.length - 1 && <KeyboardArrowRightIcon />}
						</div>
					</Button>
				</div>
			</div>
			)}
		</Container>
	);
}