import React, {useState} from 'react';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import {InputLabel, Select, MenuItem, Chip, useTheme, OutlinedInput, Button} from '@material-ui/core'

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const names = [
    'Contemporary & Modern',
    'Classical',
    'Christian',
    'Jazz & Latin',
    'Christmas & Holidayr',
    'Other'
]

function getStyles(name, personName, theme) {
    return {
      fontWeight:
        personName.indexOf(name) === -1
          ? theme.typography.fontWeightRegular
          : theme.typography.fontWeightMedium,
    };
  }

const formGroup1 = {
    width: '100%',
    display: 'grid',
    alignItems: 'center',
    gridTemplateColumns: '1fr',
    gridColumnGap: '10px',
}

const formGroup2 = {
    width: '100%',
    display: 'grid',
    alignItems: 'center',
    gridTemplateColumns: '1fr 1fr',
    gridColumnGap: '10px',
    paddingTop: 10
}

const formContentStyle = {
    width: '97%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-around',
    overflow: '',
    margin: 10,
    paddingTop: 10
}


const formSteps = [
  "Enter  course description",
  "Enter course difficulty",
  "Enter course category",
  "Enter the maximum number of students allowed in the course",
  "Select search tags",
  "Proceed..."
]

const StepOne= ({values, setValues, personName, setPersonName, setValidate}) => {
  const theme = useTheme();

  function getError(){
    if(values.courseName.length < 8) return formSteps[0];
    else if(values.difficulty == "") return formSteps[1];
    else if(values.category == "") return formSteps[2];
    else if(values.noOfStudents == "" || values.noOfStudents < 0) return formSteps[3];
    else if(values.searchTags == "" ) return formSteps[4];
    else return formSteps[5];
  }

  const errorColor = getError() != formSteps[5] ? "red" : "green";
  setValidate(getError() != formSteps[5] ? false: true);

  const handleChange2 = (event) => {
    const {
      target: { value },
    } = event;
    setPersonName(
      // On autofill we get a the stringified value.
      typeof value === 'string' ? value.split(',') : value,
    );
    setValues({
        ...values,
        ['searchTags'] : value
    })
    
  };

  const handleChange = (event) => {
    const {name, value} = event.target;
    setValues({
        ...values,
        [name] : value
    })  
  };

  const handleSubmit = (event) =>{
    event.preventDefault();
    console.log(values);
}

  return (
      <>
        <div style={formContentStyle}>
            <h5 style={{color:errorColor}}>{getError()}</h5>
            <h2>Course Overview</h2>
            <div style={formGroup1}>
                <TextField
                required
                name="courseName"
                label="Course Name"
                variant="outlined"
                multiline
                rows={4}
                value={values.courseName}
                placeholder="I will teach ..."
                onChange={handleChange}
                />
            </div>
            
            <div style={formGroup2}>
                <Select
                name="difficulty"
                value={values.difficulty}
                label="Age"
                variant="outlined"
                placeholder="Difficulty"
                onChange={handleChange}
                displayEmpty
                >
                <MenuItem value="">
                    <em>Difficulty</em>
                </MenuItem>
                <MenuItem value={1}>Easy</MenuItem>
                <MenuItem value={2}>Moderate</MenuItem>
                <MenuItem value={3}>Advanced</MenuItem>
                </Select>
                <TextField
                name="category"
                label="Category"
                variant="outlined"
                autoComplete="off"
                value={values.category}
                onChange={handleChange}
                />
            </div>

            <div style={formGroup2}>
                <TextField
                name="noOfStudents"
                label="Max no of Students"
                variant="outlined"
                value={values.noOfStudents}
                onChange={handleChange}
                type="number"
                style={{top:"8px"}}
                />
                <div>
                <InputLabel id="demo-multiple-chip-label">Search Tags</InputLabel>
                    <Select
                    name="searchTags"
                    multiple
                    value={personName}
                    onChange={handleChange2}
                    style={{width:"100%"}}
                    input={<OutlinedInput id="select-multiple-chip" label="Chip" />}
                    renderValue={(selected) => (
                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                        {selected.map((value) => (
                            <Chip key={value} label={value} />
                        ))}
                        </Box>
                    )}
                    MenuProps={MenuProps}
                    >
                    {names.map((name) => (
                        <MenuItem
                        key={name}
                        value={name}
                        style={getStyles(name, personName, theme)}
                        >
                        {name}
                        </MenuItem>
                    ))}
                    </Select>
                </div>
            </div>
        </div>
      </>
  );
}

export default StepOne;